.place{
    &__container{
        &__img{
            height: 50vh;
            background-size: cover;
            background-position: center;
            position: relative;
            z-index: -1;
            &::before{
                content: "";
                width: 100%;
                height:100%;
                background-color: $dark;
                opacity: 0.2;
                position: absolute;
                top:0;
            }
            &::after{
                content: "";
                width: 100%;
                height: 5px;
                background-color: $dark;
                opacity: 0.8;
                position: absolute;
                bottom:0px;
            }
        }
        &__text{
            display: flex;
            flex-direction: column;
            margin: 15px 20px;
            &__list{
                display: flex;
                flex-direction: row;
                &__tab{
                    font-size: 10px;
                    font-weight: 500;
                    display: block;
                    padding: 0.3em 0.85em;
                    margin-left: 0.5em;
                    border: 2px solid #001c3d;
                    border-radius: 2em;
                }
            }
            &__name{
                @include title-style;
                margin-bottom: 5px;
            }
        }
        &__map{
            display: flex;
            align-items: center;
            justify-content: center;            
            background-size: cover;
            background-position: center;
            color: $light;
            margin: 15px 20px;
            border-radius: 15px;
            overflow: hidden;
            min-height: 180px;
            position: relative;
            z-index: -1;
            &::before{
                content: "";
                width: 100%;
                height:100%;
                background-color: $dark;
                opacity: 0.2;
                position: absolute;
                top:0;
            }
        }
        &__contact{
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            a{
                display: flex;
                //border: solid 3px $dark;
                background-color: $dark;
                color: $light;
                padding: 15px;
                border-radius: 50%;
                margin: 0px 10px;
                font-size: 22px;
            }
        }
        &__notice{
            display: flex;
            justify-content: center;
            margin-top: 12px;
            span{
                font-size: 12px;
                font-weight: 500;
                color: $dark;
                opacity: 0.6;
            }
        }
    }
}