html{
    scroll-behavior: smooth;
}
body{
    margin: 0;
    padding: 0;
    background: $light;
    font-family: 'Tajawal';
    user-select: none;
}
*{
    box-sizing: border-box;
}
ul {
    margin-block: 0;
    list-style: none;
    margin-inline: 0;
    padding: 0;
    display: inline;
}
li {
    list-style: none;
    display: inline;
}

a {
    color: inherit;
    text-decoration: none;
    cursor: pointer;
}
.wait{
    display: flex;
    justify-content: center;
    height: calc(100vh - 76px);
    align-content: center;
    flex-wrap: wrap;
    &__loading {
        display: inline-block;
        position: relative;
        width: 80px;
        height: 80px;
        div {
            position: absolute;
            border: 4px solid $dark;
            opacity: 1;
            border-radius: 50%;
            animation: loading 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
            &:nth-child(2) {
                animation-delay: -0.5s;
              }
          }
        
      }
}
  @keyframes loading {
    0% {
      top: 40px;
      left: 40px;
      width: 0;
      height: 0;
      opacity: 0;
    }
    4.9% {
      top: 40px;
      left: 40px;
      width: 0;
      height: 0;
      opacity: 0;
    }
    5% {
      top: 40px;
      left: 40px;
      width: 0;
      height: 0;
      opacity: 1;
    }
    100% {
      top: 0px;
      left: 0px;
      width: 80px;
      height: 80px;
      opacity: 0;
    }
  }
  