.home{
    margin: 0px 20px 25px;
    &__banner{
        padding: 75px;
        text-align: center;
        background: $dark;
        color: $light;
        margin: 20px 0px;
        border-radius: 12px;
    }
    &__explore, &__guide{
        display: flex;
        flex-direction: column;
        &__title{
            @include headline-style;
        }
        &__explanation{
            @include paragraph-style;
            font-size: 14pt;
        }
    }
    &__carousel {
        display: flex;
        overflow-x: scroll;
        padding-left: 75px;
        scroll-snap-type: x mandatory;
        scroll-padding: 0px;
        gap: 12px;
        margin: 8px 0px 15px;
        border-radius: 8px;
        &::-webkit-scrollbar{
            display: none;
        }
        &__card{
            flex: 0 0 100%;
            height: 180px;
            border-radius: 8px;
            scroll-snap-align: center;
            background-size: cover;
            background-position: center;
            position: relative;
            overflow: hidden;
            z-index: -1;
            &:first-child{
                scroll-snap-align: start;
            }
            &::before{
                content: "";
                position: absolute;
                background-color: $darkShadow;
                height: 100%;
                width: 100%;
            }
        }
    }
    &__tags{
        margin: 10px 0px;
        &__list{
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            margin-bottom: 15px;
            &__item{
                display: flex;
                justify-content: center;
                align-items: center;
                padding: 5px;
                border: solid 2px;
                border-right: none;
                border-color: $medium;
                width: 100%;
                font-size: 16px;
                font-weight: 500;
                color: $dark;
                &:first-child{
                    border-right:solid 2px $medium ;
                    border-radius: 0px 10px 10px 0px;
                }
                &:nth-last-child(+2){
                    border-radius: 10px 0px 0px 10px;
                }
                &:last-child{
                    border-radius: 10px ;
                    background-color: $medium;
                    color: $light;
                    margin-right: 5px;
                    font-size: 22px;
                    padding: 0px;
                    }
            &:has(> input:checked){
                background-color: $medium;
                color: $light;
            }
            }

        }
    }
    &__result {
        &__list{
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 10px;
            &__card {
                box-shadow: 0 2px 6px 0 $darkShadow;
                border-radius: 10px 10px 0px 0px;
                overflow: hidden;
                &__img{
                    height: 20vh;
                    background-size: cover;
                    background-position: center;
                    position: relative;
                    z-index: -1;
                    &::before{
                        position: absolute;
                        content: "";
                        background-color: $dark;
                        width: 100%;
                        height: 100%;
                        opacity: 0.4;
                    }
                }
                &__container {
                    display: flex;
                    width: 100%;
                    position: relative;
                    z-index: -1;
                    p{
                        font-weight: 600;
                        font-size: 14px;
                        color: $dark;
                        opacity: 0.6;
                        padding: 0px 10px 0px 10px;
                    }
                    &::before{
                        content: "";
                        width: 100%;
                        height: 2px;
                        position: absolute;
                        top: 0;
                        background-color: $dark;
                        opacity: 0.4;
    
    
                    }
                }
            }
        }
    }
}
.drawer{
    position: fixed;
	bottom: -60vh;
	left: 0;
	height: 60vh;
	background-color: white;
	z-index: 1100;
	transition: transform $animation;
	box-shadow: 0 2px 6px $darkShadow;
    border-radius: 50px 50px 0px 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
	&.open {
		transform: translateY(-60vh);
	}
    &__shadow{
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
    }
    &__grabber{
        border: 0;
        margin: 20px 0px 0px;
        padding-bottom: 10px;
        width: 80px;
        &:before {
            border-top: 4px solid $medium;
            content: "";
            display: block;
            border-radius: 20px
        }
    }
    &__title{
        @include headline-style;
        font-size: 20px;
        margin: 10px 0px;
    }
    &__list{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        margin:5px 15px 0px 0px;
        &__tab{
            font-size: 16px;
            font-weight: 500;
            display: block;
            padding: 0.3em 1.2em;
            margin:0em 0em 0.5em 0.85em;
            border: 2px solid $medium;
            border-radius: 2em;
            opacity: 0.5;
            &:has(> input:checked){
                background-color: $medium;
                color: $light;
                opacity: 1;
            }
        }
    }
}