.footer{
    margin: 20px 20px 10px;
    &__separator{
        border: 0;
        margin: 0px 15px;
        padding-bottom: 10px;
        &:before {
            border-top: 2px solid $medium;
            content: "";
            display: block;
        }
    }
    &__sm{
        display: flex;
        flex-direction: row;
        justify-content: center;
        margin: 10px 0px;
        a{
            display: flex;
            padding: 5px 10px;
            border-radius: 50%;
            color: $medium;
            font-size: 28px;
            margin-left: 5px;
        }
    }
    &__copyright{
        display: flex;
        justify-content: center;
        margin-top: 10px;
        color: $medium;
        font-size: 12px;
    }
}