.navigation{
    position: sticky;
    top: 0px;
    background: $light;
    box-shadow: 0 2px 6px 0 $darkShadow;
    .header{
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        justify-content: space-between;
        z-index: 1;
        padding: 20px 0px;
        margin: 0px 20px;
        &__hamburger{
            display:flex;
             flex-direction:column;
             direction: ltr;
             width:30px;
             cursor:pointer;
             span{
                background: $dark;
                border-radius:10px;
                height:3px;
                margin: 3px 0;
                transition: .4s  cubic-bezier(0.68, -0.6, 0.32, 1.6);
              
              }
              
              
              span:nth-of-type(1){
                width:50%;
                
              }
              
              span:nth-of-type(2){
                width:100%;
              }
              
              
              span:nth-of-type(3){
                width:75%;
               
              }
              &__checked{
                   span:nth-of-type(1){
                       transform-origin:bottom;
                       transform:rotatez(45deg) translate(3px,0px)
                   }
                   
                   
                   span:nth-of-type(2){
                       
                       transform-origin:top;
                       transform:rotatez(-45deg)
                   }
                   
                   
                   span:nth-of-type(3){
                   
                       transform-origin:bottom;
                       width:50%;
                       transform: translate(14px,-4px) rotatez(45deg);
                   
                   }
           }
        }
        &__logo{
            font-size: 24px;
            font-weight: 700;
            color: $dark;
            margin: 0;
        }
        &__add{
            font-size: 32px;
            font-weight: 700;
        }
           
    }
    .sidebar{
        position: relative;
        z-index: 1000 !important;
        &__container{
            position: fixed;
            height: 100%;
            top: 72px !important;
            bottom: auto !important;
            left: 0;
            right: 0;
            padding: 32px 0px;
            background-color: $light;
            &__menu {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                    li{
                        a{
                            padding: 12px 32px;
                            font-size: 22px;
                            font-weight: 500;      
                            color: $dark;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            i{
                                margin-left: 10px;
                                font-size: 24px;
                                padding: 5px;
                            }
                        }
                    }
            }
            &__separator{
                border: 0;
                margin: 15px 80px;
                padding-bottom: 10px;
                &:before {
                    border-top: 2px solid $dark;
                    opacity: 0.1;
                    content: "";
                    display: block;
                }
            }
        }
    }
}