$dark : #001c3d;
$darkShadow : #001c3d3d;
$medium : #617287;
$accent : #ccccff;
$light : #f2f3f4;
$animation: .3s 0s cubic-bezier(.55,0,.1,1);
@mixin headline-style {
    font-size: 24pt;
    font-weight: 700;
    color: $dark;
}
@mixin title-style {
    font-size:32pt;
    font-weight: 700;
    color: $dark;
}
@mixin paragraph-style {
    font-size: 16pt;
    font-weight: 400;
    color: $medium;
}
